@charset "UTF-8";
.operation-list_item {
  display: flex;
  flex-direction: column;
  margin-bottom: 10px;
  padding: 10px;
  border: 1px solid #ccc;
  border-radius: 3px;
  box-shadow: 2px 2px 5px rgba(0, 0, 0, 0.3);
  animation: slide-up 0.4s ease; }
  .operation-list_item :last-child {
    margin-bottom: 0; }
  .operation-list_item .item_row {
    display: inline-flex;
    flex-wrap: wrap; }
    .operation-list_item .item_row.controls {
      justify-content: flex-end; }
  .operation-list_item.debit {
    background: #bfffcb; }
  .operation-list_item.credit {
    background: #ffaeae; }
  .operation-list_item .time {
    position: relative;
    display: inline-block;
    width: intrinsic;
    /* Safari/WebKit используют нестандартное имя */
    width: -moz-max-content;
    /* Firefox/Gecko */
    width: -webkit-max-content;
    /* Chrome */
    border-bottom: 1px dotted black;
    /* If you want dots under the hoverable text */ }
    .operation-list_item .time .tooltip {
      visibility: hidden;
      background-color: black;
      opacity: 0.9;
      color: #fff;
      text-align: center;
      padding: 5px 0;
      border-radius: 6px;
      position: absolute;
      width: 150px;
      bottom: 100%;
      left: 75%;
      margin-left: -60px;
      z-index: 1; }
    .operation-list_item .time:hover .tooltip {
      visibility: visible; }
  .operation-list_item:hover .remove,
  .operation-list_item:hover .edit {
    opacity: 0.5; }
  .operation-list_item .remove,
  .operation-list_item .edit {
    opacity: 0;
    user-select: none; }
    @media only screen and (max-width: 768px) {
      .operation-list_item .remove,
      .operation-list_item .edit {
        opacity: 0.5; } }
    .operation-list_item .remove:hover, .operation-list_item .remove:active,
    .operation-list_item .edit:hover,
    .operation-list_item .edit:active {
      opacity: 1;
      cursor: pointer; }

@keyframes slide-up {
  0% {
    opacity: 0;
    transform: translateY(20px); }
  100% {
    opacity: 1;
    transform: translateY(0); } }
